// actions/userActions.js
import { SET_CURRENT_USER, CLEAR_CURRENT_USER } from '../type';

export const setCurrentUser = (user) => {
  return {
    type: SET_CURRENT_USER,
    payload: user,
  };
};

export const clearCurrentUser = () => {
  return {
    type: CLEAR_CURRENT_USER,
  };
};


